.form-switch {
  cursor: pointer;
  color: #fff;
  align-items: center;
  display: flex;
}

.form-switch span {
  width: 33px;
  height: 17px;
  vertical-align: text-bottom;
  background-color: #e6e6e6;
  border-radius: 23px;
  margin-right: 5px;
  transition: all .3s linear;
  display: inline-block;
  position: relative;
}

.form-switch span:before {
  content: "";
  width: 30px;
  height: 15px;
  background-color: #fff;
  border-radius: 10px;
  transition: all .25s linear;
  position: absolute;
  left: 0;
  transform: translate3d(2px, 2px, 0)scale3d(1, 1, 1);
}

.form-switch span:after {
  content: "";
  width: 13px;
  height: 13px;
  background-color: #fff;
  border-radius: 11px;
  transition: all .2s ease-in-out;
  position: absolute;
  left: 0;
  transform: translate3d(2px, 2px, 0);
  box-shadow: 0 2px 2px rgba(0, 0, 0, .24);
}

.form-switch:active span:after {
  width: 20px;
  transform: translate3d(2px, 2px, 0);
}

.form-switch:active input:checked + span:after {
  transform: translate3d(13px, 2px, 0);
}

.form-switch input {
  display: none;
}

.form-switch input:checked + span {
  background-color: #545454;
}

.form-switch input:checked + span:before {
  transform: translate3d(13px, 2px, 0)scale3d(0, 0, 0);
}

.form-switch input:checked + span:after {
  transform: translate3d(18px, 2px, 0);
}

.theme-switch__icon {
  fill: currentColor;
  height: 15px;
  width: 15px;
}

.theme-switch__icon:hover, .form-switch input:checked ~ .theme-switch__icon {
  fill: #ff6b08;
}

