.preloader {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
  /* фоновый цвет */
  background: #fff;
  z-index: 1001;
  opacity: 0.5;
}

.preloader__image {
  position: relative;
  top: 50%;
  left: 50%;
  width: 70px;
  height: 70px;
  transform: translate(-50%, -50%);
  text-align: center;
  animation: spin 2s infinite linear;
  visibility: visible;
}

@keyframes spin {
  from {
    transform: translate(-50%, -50%) rotate(0deg);
  }
  to {
    transform: translate(-50%, -50%) rotate(360deg);
  }
}

.loaded_hiding .preloader {
  transition: 0.3s opacity;
  opacity: 0.5;
}

.done {
  opacity: 0;
  visibility: hidden;
}

@keyframes spinner-border {
  100% {
    transform: rotate(360deg);
  }
}
